<template lang="pug">
    v-list-item
        v-list-item-content
            v-card
                v-card-title
                    span {{fileName}}
                v-container
                v-row 
                    v-col(cols="12" xs="6" sm="6" lg="3")
                        span 下单数量:
                    v-col(cols="12" xs="6" sm="6" lg="3")
                        v-chip {{amount}}
                    v-col(cols="12" xs="6" sm="6" lg="3")
                        span 当前状态：
                    v-col(cols="12" xs="6" sm="6" lg="3")
                        v-chip {{status}}

                v-card-actions
                    v-row
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="success" dark @click="start") 启动
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="warnning" dark @click="stop") 暂停
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="warnning" dark @click="edit") 编辑
                        v-col(cols="12" xs="6" sm="6" lg="3")
                            v-btn(color="error" dark @click="remove") 删除
                    
                    

        //-     v-list-item-title
        //-         h3 {{fileName}}
        //-     v-row
        //-         v-col(cols="12" sm="12" lg="3")
        //-             span 下单数量:
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-chip {{amount}}
        //-         v-col(cols="12" sm="12" lg="3")
        //-             span 当前状态：
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-chip {{status}}
        //- v-list-item-action
        //-     v-row
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-btn(color="success" dark @click="start") 启动
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-btn(color="warnning" dark @click="stop") 暂停
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-btn(color="warnning" dark @click="edit") 编辑
        //-         v-col(cols="12" sm="12" lg="3")
        //-             v-btn(color="error" dark @click="remove") 删除


</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    name: "dbmstatuslistitem",
    props:{
        fileName:{
            type:String,
            default:"",
        },
        status:{
            type:String,
            default:"",
        },
        amount:{
            type:String,
            default:"",
        }
    },

    methods:{
        go_updateconfig(){
            this.$router.push({path:"/dbm/updateconfig"});
        },
        start(){
            api.DBMStartOne({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        stop(){
            api.DBMStopOne({FileName:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        remove(){
            api.DBMDeleteOne({filename:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        edit(){
            console.log(this.fileName)
            api.DBMGetOne({filename:this.fileName}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("exbot/update_upexconfig",res.data)
                    this.go_updateconfig()
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    },



    
}
</script>


